import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-present-ataque',
  templateUrl: './present-ataque.component.html',
  styleUrls: ['./present-ataque.component.scss'],
})
export class PresentAtaqueComponent implements OnInit {

  @Input('item')
  item;

  @Input('fullPresentation')
  fullPresentation = false;


  constructor(
    private modal: ModalController,
  ) { }

  ngOnInit() {
  }

  close() {
    this.modal.dismiss();
  }

}
