import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DetalharService {

  private id: string;
  private tipoItem: number;

  constructor() { }

  getId() {
    return this.id;
  }

  setId(id: string) {
    this.id = id;
  }

  getTipoItem() {
    return this.tipoItem;
  }

  setTipoItem(tipoItem: number) {
    this.tipoItem = tipoItem;
  }
}
